html {
  --colorPrincipal: #0098ce;
  --colorPrincipalClaro: #cceaf5;
  --colorBlanco: #ffffff;
  --colorNegro: #000000; 
  --colorGrisSuave: #f0f1f3;
  --colorGrisClaro: #a7a7a7;
  --colorGrisMedio: #757574;
  --colorGrisOscuro: #3b3b3a;
  --colorGrisTransparente: rgb(255, 255, 255,50%);
  --colorAzulContraste: #02488e;
  --colorAmarilloContraste: #ffdd00;
  --colorError: #e4032d;
  --colorOk: #009d4e;
  --fontPrincipal:'Tahoma', sans-serif; 
  --h1: 48.83px ;
  --h2: 39.06px ;
  --h3: 31.25px ;
  --h4: 25px ;
  --h5: 20px ;
  --h6: 16px ;
  --h7: 14px ;
  --p:  16px ;
  --notification:12px;
  --li:  14px ;
  --bold : bold;
  --Sombra: 5px 5px 10px var(--colorGrisClaro); 
  --sombraSmall: 2px 2px 2px var(--colorGrisClaro); 
  --widthCartList: 80%;
}

body {
  margin: 0;
  padding: 0;
  font-family: 'Tahoma', sans-serif !important;
  font-family: var(--fontPrincipal) !important;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;  
  background-color: #cceaf5 !important;  
  background-color: var(--colorPrincipalClaro) !important;  
}

body  ::selection {
  color: #02488e;
  color: var(--colorAzulContraste);
  background: #ffffff;
  background: var(--colorBlanco);
}

body  ::-moz-selection {
  color: #02488e;
  color: var(--colorAzulContraste);
  background: #ffffff;
  background: var(--colorBlanco);
}

body h1 {
  font-size: 48.83px;
  font-size: var(--h1);
}

body h2 {
  font-size: 39.06px;
  font-size: var(--h2);
}

body h3 {
  font-size: 31.25px;
  font-size: var(--h3);
}

body h4 {
  font-size: 25px;
  font-size: var(--h4);
}

body h5 {
  font-size: 20px;
  font-size: var(--h5);
}

body h6 {
  font-size: 16px;
  font-size: var(--h6);
}

body p {
  margin: 0;
  padding: 0;
  font-size: 16px;
  font-size: var(--p);
}

body a {
  font-size: 16px;
  font-size: var(--p);
  color: #3b3b3a;
  color: var(--colorGrisOscuro);
  text-decoration: none;
}

body a:hover {
  color: #0098ce;
  color: var(--colorPrincipal);
}

li {
  font-size: 14px;
  font-size: var(--li);
  list-style: none;
}

* button {
  background-color:transparent;
  border-style: none;
}

.noBorder {
  border-bottom: transparent !important;
}

.modal-header {
  background-color: #02488e !important;
  background-color: var(--colorAzulContraste) !important;
  border-radius: 5px !important;
}

.btn-close:hover {
  color: #ffffff !important;
  color: var(--colorBlanco) !important;
}

.spacerL {
  margin-top:10px;
  margin-bottom: 10px;
}
.contenedorNav {
    display: flex;
    flex-direction: row;
    width: 95% !important;
    height: auto;
    justify-content: flex-start;
    align-items: flex-start;
}

.contenedorNav--logo {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-content: center;
    height: 5rem;
}

.navbarLogo {
    width:13rem;
    height: auto;
}

.contenedorNav--mainMenu {
    display: flex;
    flex-direction: column;
    height: 5rem;
    justify-content: space-between;
    align-content: center;
}

.mainMenu--top {
    display: flex;
    flex-direction: row;
    justify-content:center;
    align-items: center;
    grid-gap: 10px;
    gap: 10px;
}

.mainMenu--top--search--searchBtn {
    background-color: var(--colorAzulContraste) !important;
    color: var(--colorGrisClaro) !important;
}

.mainMenu--top--search--searchBtn:hover {
    color: var(--colorBlanco) !important;
}

.mainMenu--bottom {
    display: flex;
    flex-direction: row;
}

.mainMenu--bottom--linksMenu {
    display: flex ;
    flex-direction: row ;
    justify-content: flex-start;
    grid-gap: 10px;
    gap: 10px;
}

.linksMenu--link  {
    height: 70%;
    text-align: left;
    border: solid 1px transparent;
    border-radius: 5px;
    background-color: var(--colorGrisTransparente);
    padding-left: 5px;
    padding-right: 5px;
}

.linksMenu--link a {
    color: var(--colorGrisMedio);
}

.linksMenu--categoriesList {
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    border: solid 1px var(--colorGrisClaro);
    border-radius: 5px;
    width: 40% ;
    height: auto;
}

.categoriesList--collapsableList {
    width: 100% ;
    height: auto;
}

.collapsableList--listaCategorias {
    padding-left: 10px;
}

.categoriesList--btnDropdown {
    text-align: center;
    color: var(--colorGrisOscuro) !important;
}

.contenedorNav--userControls {
    display: flex;
    flex-direction: column;
    justify-content:flex-start;
    align-items: center;
    color: var(--colorNegro);
    height: 5rem;
    border: solid 1px;
    border-radius: 5px;
    margin-left: 10px;
    margin-right: 10px;
}

.userControls--resgistrarse a {
    font-size: var(--h7) !important;
}

.userControls--sesion, .userControls--resgistrarse {
    display: flex;
    flex-direction: row;
}

.contenedorNav--cartControls {
    display: flex;
    flex-direction: column;
    fill: var(--colorGrisOscuro);
    justify-content: flex-start;
    align-items: flex-start;
    height: 5rem;
    padding-top: 20px;
    margin-left: 18px;
}

.cartControls--container {
    position: absolute;
}

.cartControls--qTip {
    visibility: visible;
    text-align: center;
    position: relative;
    font-size: var(--notification);
    position: absolute;
    left: 70%;
    top: -5px;
    border: solid 1px transparent;
    border-radius: 100%;
    background-color: var(--colorAzulContraste);
    color: var(--colorBlanco);
    width: 1.25rem;
    height: 1.25rem;
}

.carousel-indicators button {
    width: 10px !important;
    height: 10px !important;
    border-radius: 10px !important;
    border-style: hidden !important;
}
.charsetModal {
    color: var(--colorNegro);
}

.modal-header {
    background-color: var(--colorPrincipal);
    color: var(--colorBlanco);
    width: 100%;
    border-radius: 10px ;
    border-style: hidden ;
}

.modalBodyText {
    margin-top: 1rem;
    text-align: left;
    height: auto;
}

.modalBodyText a:hover {
    color: var(--colorPrincipal) !important;
}

.modalFooterBtn {
    background-color: var(--colorAzulContraste);
    color: var(--colorBlanco);
    padding: 1rem;
    border-radius: 10px ;
    border-style: hidden ;
}

.charset {
    display: flex;
    flex-direction: column;
    width: 25%;
    height: auto;
    justify-content: center;
    align-items: center;
    grid-gap: 5px;
    gap: 5px;
}

.charsetSeparator {
    border-left: solid 1px var(--colorAzulContraste);
    margin-top: 20px;
    margin-bottom: 20px;
}

.charsetIconContainer {
    display: flex;
    align-items: center;
    justify-content: center;
    height: auto;
}

.charsetIcon {
    width: 30px;
    height: auto;
    color: var(--colorAzulContraste);
}

.charsetIconFront {
    position: relative;
    left: -10px;
    bottom: -10px;;
}

.charsetLink {
    width: 100%;
    color: var(--colorBlanco);
    font-size: var(--notification);
}

.charsetLink:hover {
    color:var(--colorAzulContraste);
}

.modal--tablaComisiones {
    width: 100%;
    height: auto;
    display: grid;
    grid:
      "valorTitle comisionTitle" 
      "valor comision"; 
    grid-gap: 8px; 
    gap: 8px;
    font-size: var(--h7);
}

.calcComision, .calcComision p {
    font-size: var(--h7);
}


.comision, .comisionTitle  {
    text-align: right;
}

.calcComision {
    display: flex ;
    flex-direction: column ;
}

.calcComisionLine {
    display: flex ;
    flex-direction: row ;
}

.calcComisionInput {
    width: 40% ;
}

